/*    */

.calendar_container {
    padding: 2rem;
    min-height: calc(100vh - 100px);
    background: var(--bg-primary);
}


/*   */

.dashboard_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    position: relative;
    z-index: 1;
    flex-shrink: 0;
    width: 100%;
}

.header_content {
    display: flex;
    flex-direction: column;
}

.dashboard_title {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
    color: var(--text-primary);
    text-align: left;
    transition: color 0.3s ease;
    position: relative;
    z-index: 1;
    font-family: var(--font-primary);
}

.dashboard_date {
    font-size: 1rem;
    color: var(--text-secondary);
    text-align: left;
    margin-bottom: 0;
    position: relative;
    z-index: 1;
    font-family: var(--font-mono);
}

.header_controls {
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.customize_btn {
    padding: 0.75rem 1.5rem;
    border-radius: var(--border-radius-md);
    background-color: var(--bg-secondary);
    color: var(--text-primary);
    border: 1px solid var(--border-color);
    font-weight: 500;
    font-size: 0.875rem;
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.customize_btn:hover {
    background-color: var(--bg-tertiary);
    border-color: var(--accent-color);
}

.save_btn {
    background-color: var(--accent-color);
    color: var(--text-primary-inverse);
    border-color: var(--accent-color);
}

.save_btn:hover {
    background-color: var(--accent-hover);
}

.spinning {
    animation: spin 1s linear infinite;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}


/*    */

.calendar_wrapper {
    background: var(--bg-secondary);
    border-radius: var(--border-radius-lg);
    border: 1px solid var(--border-color);
    overflow: hidden;
    margin-bottom: 1.5rem;
}


/* react-big-calendar   */

.calendar_wrapper .rbc-calendar {
    font-family: var(--font-secondary);
    color: var(--text-primary);
}

.calendar_wrapper .rbc-header {
    background: var(--bg-tertiary);
    color: var(--text-primary);
    font-weight: 600;
    padding: 1rem 0.5rem;
    border-bottom: 1px solid var(--border-color);
    font-size: 0.875rem;
}

.calendar_wrapper .rbc-month-view {
    border: none;
}

.calendar_wrapper .rbc-date-cell {
    padding: 0.5rem;
    color: var(--text-primary);
    border-right: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-date-cell:last-child {
    border-right: none;
}

.calendar_wrapper .rbc-day-bg {
    border-right: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-day-bg:last-child {
    border-right: none;
}

.calendar_wrapper .rbc-off-range-bg {
    background: var(--bg-primary);
}

.calendar_wrapper .rbc-today {
    background-color: rgba(var(--accent-color-rgb), 0.05);
}

.calendar_wrapper .rbc-event {
    background-color: var(--accent-color);
    color: var(--text-primary-inverse);
    border: none;
    border-radius: 0.25rem;
    font-size: 0.75rem;
    padding: 0.125rem 0.375rem;
    font-weight: 500;
    margin: 0.125rem 0;
}

.calendar_wrapper .rbc-event:hover {
    opacity: 0.8;
}

.calendar_wrapper .rbc-selected {
    background-color: var(--accent-color) !important;
}

.calendar_wrapper .rbc-slot-selection {
    background-color: rgba(var(--accent-color-rgb), 0.1);
}

.calendar_wrapper .rbc-toolbar {
    margin-bottom: 1rem;
    padding: 1rem;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-tertiary);
}

.calendar_wrapper .rbc-toolbar button {
    color: var(--text-primary);
    border: 1px solid var(--border-color);
    background: var(--bg-secondary);
    padding: 0.5rem 1rem;
    border-radius: var(--border-radius-sm);
    font-size: 0.875rem;
    transition: all 0.2s ease;
    margin: 0 0.125rem;
}

.calendar_wrapper .rbc-toolbar button:hover {
    background: var(--bg-tertiary);
    border-color: var(--accent-color);
}

.calendar_wrapper .rbc-toolbar button.rbc-active {
    background: var(--accent-color);
    color: var(--text-primary-inverse);
    border-color: var(--accent-color);
}

.calendar_wrapper .rbc-toolbar-label {
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 1rem;
}


/*   */

.legend {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
    padding: 1rem;
    background: var(--bg-secondary);
    border-radius: var(--border-radius-md);
    border: 1px solid var(--border-color);
    margin-bottom: 1rem;
}

.legend_item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.8rem;
    color: var(--text-secondary);
}

.legend_color {
    width: 0.75rem;
    height: 0.75rem;
    border-radius: 0.125rem;
    flex-shrink: 0;
}


/*   */

.toast {
    position: fixed;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 1.5rem;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius-lg);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.12);
    z-index: 1000;
    font-size: 0.875rem;
    color: var(--text-primary);
    min-width: 200px;
    /* max-width: 300px; */
    backdrop-filter: blur(16px);
    animation: toastSlideIn 0.4s cubic-bezier(0, 0, 0.2, 1) forwards;
}

.toast.exit {
    animation: toastSlideOut 0.25s cubic-bezier(0.4, 0, 1, 1) forwards;
}

.toast svg {
    font-size: 1.125rem;
    color: var(--accent-color);
    flex-shrink: 0;
}

.toast span:first-of-type {
    flex: 1;
    font-weight: 500;
}

.toast_close {
    background: none;
    border: none;
    color: var(--text-secondary);
    cursor: pointer;
    font-size: 1.25rem;
    padding: 0;
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: all 0.2s cubic-bezier(0, 0, 0.2, 1);
    flex-shrink: 0;
}

.toast_close:hover {
    background: var(--bg-tertiary);
    color: var(--text-primary);
}

@keyframes toastSlideIn {
    0% {
        opacity: 0;
        transform: translateX(-50%) translateY(100%);
    }
    100% {
        opacity: 1;
        transform: translateX(-50%) translateY(0);
    }
}

@keyframes toastSlideOut {
    0% {
        opacity: 1;
        transform: translateX(-50%) translateY(0);
    }
    100% {
        opacity: 0;
        transform: translateX(-50%) translateY(-20%);
    }
}


/*   */

@media (max-width: 768px) {
    .calendar_container {
        padding: 1rem;
    }
    /*    */
    .toast {
        bottom: 1rem;
        left: 1rem;
        right: 1rem;
        transform: none;
        padding: 0.75rem;
    }
    .toast svg {
        font-size: 1rem;
    }
    .toast span:first-of-type {
        font-size: 0.8rem;
    }
    /*    */
    .dashboard_header {
        display: none;
    }
    /*    */
    .calendar_navigation {
        flex-direction: column;
        gap: 1rem;
        padding: 1rem;
        margin-top: 0;
    }
    .nav_left {
        width: 100%;
        justify-content: space-between;
        gap: 1rem;
    }
    .nav_arrows {
        flex: 1;
        justify-content: center;
        gap: 1rem;
    }
    .current_period {
        font-size: 1rem;
        min-width: auto;
        flex: 1;
    }
    .today_btn {
        padding: 0.5rem 0.75rem;
        font-size: 0.8rem;
        flex-shrink: 0;
    }
    .nav_btn {
        width: 2.5rem;
        height: 2.5rem;
        font-size: 1.125rem;
    }
    .view_buttons {
        width: 100%;
        gap: 0.125rem;
    }
    .view_btn {
        flex: 1;
        padding: 0.6rem 0.5rem;
        font-size: 0.8rem;
        text-align: center;
    }
    /*    -      */
    .calendar_wrapper {
        margin-bottom: 1rem;
    }
    .legend {
        gap: 0.5rem;
        font-size: 0.75rem;
        padding: 0.75rem;
    }
    .legend_item {
        font-size: 0.7rem;
    }
    .legend_color {
        width: 0.6rem;
        height: 0.6rem;
    }
    /*     */
    .calendar_wrapper .rbc-resource-header {
        padding: 0.5rem 0.25rem;
        font-size: 0.75rem;
        writing-mode: vertical-rl;
        text-orientation: mixed;
        width: 60px;
    }
    .calendar_wrapper .rbc-time-header-cell {
        font-size: 0.7rem;
        padding: 0.25rem;
    }
}


/*     */

@media (max-width: 480px) {
    .calendar_container {
        padding: 0.75rem;
    }
    .calendar_navigation {
        padding: 0.75rem;
    }
    .nav_left {
        flex-direction: column;
        gap: 0.75rem;
        align-items: stretch;
    }
    .nav_arrows {
        order: 1;
    }
    .today_btn {
        order: 2;
        align-self: center;
    }
    .current_period {
        font-size: 0.9rem;
        order: 0;
        text-align: center;
        padding: 0.5rem;
        background: var(--bg-tertiary);
        border-radius: var(--border-radius-sm);
    }
    .view_btn {
        font-size: 0.75rem;
        padding: 0.5rem 0.25rem;
    }
}


/*    */

.calendar_navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem;
    background: var(--bg-secondary);
    border-radius: var(--border-radius-lg);
    border: 1px solid var(--border-color);
    margin-bottom: 1rem;
}

.nav_left {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.today_btn {
    padding: 0.5rem 1rem;
    background: var(--accent-color);
    color: var(--text-primary-inverse);
    border: none;
    border-radius: var(--border-radius-sm);
    font-size: 0.875rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
}

.today_btn:hover {
    background: var(--accent-hover);
}

.nav_arrows {
    display: flex;
    align-items: center;
    gap: 0.75rem;
}

.nav_btn {
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-tertiary);
    color: var(--text-primary);
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius-sm);
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.nav_btn:hover {
    background: var(--accent-color);
    color: var(--text-primary-inverse);
    border-color: var(--accent-color);
}

.current_period {
    font-size: 1.125rem;
    font-weight: 600;
    color: var(--text-primary);
    min-width: 8rem;
    text-align: center;
}

.view_buttons {
    display: flex;
    gap: 0.25rem;
    background: var(--bg-tertiary);
    border-radius: var(--border-radius-sm);
    padding: 0.25rem;
}

.view_btn {
    padding: 0.5rem 1rem;
    background: transparent;
    color: var(--text-secondary);
    border: none;
    border-radius: var(--border-radius-sm);
    font-size: 0.875rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
}

.view_btn:hover {
    color: var(--text-primary);
    background: rgba(var(--accent-color-rgb), 0.1);
}

.view_btn.active {
    background: var(--accent-color);
    color: var(--text-primary-inverse);
}


/*    */

.calendar_wrapper .rbc-timeline-view {
    border: none;
}

.calendar_wrapper .rbc-time-header {
    background: var(--bg-tertiary);
    border-bottom: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-time-header-gutter {
    background: var(--bg-secondary);
    border-right: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-time-header-cell {
    background: var(--bg-tertiary);
    color: var(--text-primary);
    border-right: 1px solid var(--border-color);
    font-size: 0.8rem;
    font-weight: 500;
}

.calendar_wrapper .rbc-resource-header {
    background: var(--bg-secondary);
    color: var(--text-primary);
    border-right: 1px solid var(--border-color);
    border-bottom: 1px solid var(--border-color);
    font-weight: 600;
    padding: 0.75rem;
    font-size: 0.875rem;
}

.calendar_wrapper .rbc-time-content {
    border-top: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-time-slot {
    border-right: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-timeslot-group {
    border-bottom: 1px solid var(--border-color);
}

.calendar_wrapper .rbc-current-time-indicator {
    background-color: var(--accent-color);
    height: 2px;
}


/*    */

.calendar_wrapper .rbc-event.rbc-event-continues-prior,
.calendar_wrapper .rbc-event.rbc-event-continues-after {
    border-radius: 0.25rem;
}

.calendar_wrapper .rbc-event-continues-prior {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.calendar_wrapper .rbc-event-continues-after {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}


/*     */

.calendar_wrapper .rbc-addons-dnd-drag-preview {
    background-color: var(--accent-color);
    color: var(--text-primary-inverse);
    border-radius: 0.25rem;
    opacity: 0.8;
    padding: 0.25rem 0.5rem;
    font-size: 0.75rem;
}

.calendar_wrapper .rbc-addons-dnd-resize-ns-icon {
    background-color: var(--accent-color);
}

.calendar_wrapper .rbc-addons-dnd-resize-ew-icon {
    background-color: var(--accent-color);
}